import React, { useState } from 'react';
import { reduxForm, Field } from 'redux-form';
import pStyle from './profileInfo.module.scss';
import ProfileStatusWithHooks from './ProfileStatus/profileStatusWithHooks';
import {maxLengthCreator, requiredField} from '../../../utils/validator';
import { TextArea } from '../../../common/Textarea/formChilds';
import ProfileDataForm from './profileDataForm/profileDataForm';
import { ProfileData } from './ProfileData/profileData';
import Avatar from './profileInfoAvatar/profileInfoAvatar';
import useComponentVisible from '../../../utils/opener/opener';

const maxLength = maxLengthCreator(100);
let PostsForm = ({ handleSubmit, avatar}) => {
    const {ref, isComponentVisible, setIsComponentVisible} = useComponentVisible(false);

    const changeWriteMode = (boolean) => {
        setIsComponentVisible(boolean)
    }

    return (
        <form onSubmit={handleSubmit} className={pStyle.profileFooter__postwrite}>
            <div className={pStyle.profile__addPostAvatar}>
                <img src={avatar != null ? avatar : 'https://html5css.ru/w3images/avatar2.png'} alt="myMiniAvatar" />
            </div>
            <div className={pStyle.profile__addPostTextarea} ref={ref}>
                <Field
                    name="postInput"
                    component={TextArea}
                    type="text"
                    placeholder="Чем хотите поделиться?"
                    validate={[requiredField, maxLength]}
                    
                    onClick={() => changeWriteMode(true)}
                />
                {isComponentVisible ? <button type="sumbit" >Опубликовать</button> : null}
            </div>
        </form>
        
    )
}
PostsForm = reduxForm({
    form: 'post-form'
})(PostsForm)
const ProfileInfo = ({status, statusEditMode, toggleEditModeStatus, newStatusText, updateStatus, addPost, profile, isOwner, sendPhoto, updateProfileData}) => {

    let [editMode, changeEditMode] = useState(false);

    let addNewPost = (values) => {
        addPost(values.postInput);
    }

    const onGetNewFile = (e) => {
        if (e.target.files.length) {
            sendPhoto(e.target.files[0])
        }
    }

    const onSubmit = async (formData) => {
        await updateProfileData(formData)
        changeEditMode(false)
    } 
    
    return (
        <div className={pStyle.profileInfo}>
            <Avatar isOwner={isOwner} onGetNewFile={onGetNewFile} s={pStyle} profile={profile} />
            <div className={pStyle.profileInner}>
                <div className={pStyle.profileHeader}>
                    <div className={pStyle.profileHeader__name}>
                        {profile.fullName}
                    </div>
                    <div className={pStyle.profileHeader__status}>
                        <ProfileStatusWithHooks status={status} editMode={statusEditMode} toggleEditMode={toggleEditModeStatus} newStatusText={newStatusText} updateStatus={updateStatus}/>
                    </div>
                    {
                        editMode ? 
                            <ProfileDataForm turnOffEditMode={() => changeEditMode(false)} initialValues={profile} s={pStyle} onSubmit={onSubmit} profile={profile} isOwner={isOwner} /> 
                                : 
                            <ProfileData goToEditMode={() => changeEditMode(true)} isOwner={isOwner} profile={profile} s={pStyle} />
                    }
                </div>
                <PostsForm onSubmit={addNewPost} avatar={profile.photos.small}/>
            </div>
        </div>
    )
}

export default ProfileInfo;